*:focus,
*:active,
*:hover {
	outline: none;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;

	input.text,
	input[type='text'],
	input[type='button'],
	input[type='submit'],
	.input-checkbox {
		-webkit-appearance: none;
	}

	button {
		-webkit-appearance: none;
		background: none;
		color: inherit;
		border: none;
		padding: 0;
		outline: none;
	}

	ul {
		list-style: none;


	}

	img {
		display: block;
	}

	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	-webkit-tap-highlight-color: transparent;
}

a {
	text-decoration: none;
	color: inherit;
  outline: none !important;
  outline-offset: none !important;
}

.fas {
	pointer-events: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

input[type='number'] {
	-moz-appearance: textfield;
}

input,
select,
textarea,
button {
	font-family: inherit;
}

select {
	background-color: white !important;
}

